import {Formik, 
    Form,
    }  from 'formik';
import {Button, 
    Container, 
    } from "@chakra-ui/react";
import * as Yup from 'yup';
import '../style/field.scss';
import {Page} from  "../dataStructure/page";
import {CustomCheckBox, CustomTextInput, CustomTitle} from '../components/custom';
import firebase from '../firebase';
import {useEffect, useState} from 'react';
import { ThemeContext } from '@emotion/react';





interface signUp {
changePage:(page: Page) => void,
}


const  SignUp: React.FC<signUp> = ({changePage}) => {
  
  //const emailFilter = (key:string)=> {return (key !== 'email');}
  //const emails = Object.keys(localStorage).filter(emailFilter);
  const [emailsFireStore, setEmailsFireStore ] = useState<string[]>([])

  const ref = firebase.firestore().collection('users').doc('users_document');
  console.log(ref);

  const effect = () => {
    ref.onSnapshot((querySnapshot) => {
      const emails:any[] = [];
      emails.push(querySnapshot.data()? querySnapshot.data(): '');
      setEmailsFireStore(emails[0]['email']);

    })
  }

  useEffect(() => {
    
    effect();
  }, []);




return (
    <>
    <Formik 
    initialValues={{
        firstName:'',
        lastName:'',
        email:'',
        password:'',
        passwordConfirm:'',
        acceptedTerms: false,
    }}

    validationSchema={Yup.object({
        firstName: Yup.string()
            .min(3, 'Must be at least 3 characters long.')
            .max(15, 'Must be 15 characters or less.')
            .required('Required'),
        lastName: Yup.string()
            .min(3, 'Must be at least 3 characters long.')
            .max(15, 'Must be 15 characters or less.')
            .required('Required'),
        email: Yup.string()            
            .email('Invalid email address.')
            .required('Please Enter a valid email address.')
            .notOneOf(emailsFireStore, 'This email is already in use.'),
        password: Yup.string()
            .matches(
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
                "Must Contain 8 Characters, One Uppercase, One Lowercase, One Number and one special case Character"
              )           
            .required('Please Enter your password.'),
        passwordConfirm: Yup.string()
            .when('password', (password:boolean, field: any) =>
            password ? field.required('Please confirm your password.').oneOf([Yup.ref('password')], 'You must use the same password you inserted before.') : field
            ),
        acceptedTerms: Yup.boolean()
            .oneOf([true], 'You must accept the terms and conditions.')
            .required('Required')
        

    })}
    onSubmit={(values, {setSubmitting, resetForm}) => {
        setTimeout(()=> {
            const newUser = {
                fName: values.firstName,
                lName: values.lastName,
                pssw: values.password,
                lists: {},
                list: '',
            }
            //localStorage.setItem(values.email, JSON.stringify(newUser));
            localStorage.setItem('email', values.email);

            
            ref.set(
                  {email: [values.email,...emailsFireStore]}
            ).then(() => {
              console.log('DONE EMAIL');
              ref.collection(values.email).doc('data').set(
                newUser
              ).then(() => {
                console.log('DONE DATA');
              });
              
            changePage(Page.DashBoard);
            }).catch((e) => console.log(`ERROR ${e}`));


            console.log(values);
            console.log(typeof values);
            resetForm();
            setSubmitting(false);
        }, 1500);
    }}
    >

        {props => (

            <Form>
                <Container centerContent={true}>
                <CustomTitle title='Sign Up'/>
                <CustomTextInput label='First Name' name='firstName' type='text' placeholder='First Name' handleBlur={props.handleBlur}/>                    
                <CustomTextInput label='Last Name' name='lastName' type='text' placeholder='Last Name' handleBlur={props.handleBlur}/>                    
                <CustomTextInput label='E-mail' name='email' type='text' placeholder='example@email.com' handleBlur={props.handleBlur}/>                 
                <CustomTextInput label='Password' name='password' type='password' placeholder='password' handleBlur={props.handleBlur}/>
                <CustomTextInput label='passwordConfirm' name='passwordConfirm' type='password' placeholder='password confirmation' handleBlur={props.handleBlur}/>
                <CustomCheckBox name='acceptedTerms' label='I accept the terms and conditions.' handleBlur={props.handleBlur}/>
                <Button type='submit' colorScheme='blue' className='button'> {props.isSubmitting? 'Loading...':'Create Account'}</Button>
                </Container>
            </Form>
        )}


    </Formik>            
    </>
)
}

export default SignUp
