import {CustomTitle} from '../components/custom';
import {Container, Select, HStack, VStack, Text} from "@chakra-ui/react";
import {useState} from 'react';

interface nonTransitiveDice{
  title: string;
}

const dice = {
  red : [4, 4, 4, 4, 4, 9],
  blue : [2, 2, 2, 7, 7, 7],
  olive : [0, 5, 5, 5, 5, 5],
  yellow : [3, 3, 3, 3, 8, 8],
  magenta : [1, 1, 6, 6, 6, 6],
}

const buildField = () => {
  let x = new Array(6);
  for (let i = 0; i < x.length; i++) {
    x[i] = [0,0,0,0,0,0];    
  }
  return x
}


const NonTransitiveDice: React.FC<nonTransitiveDice> =({title}) => {
  const [state, setstate] = useState<(keyof typeof dice)[]>(['red', 'red'])
  const [field, setField] = useState<(number)[][]>(buildField());
  const [hidden, setHidden] = useState(true);
  console.log(field);

  const NotHidden = () => setHidden(false);

  return (
    <>
      <CustomTitle title={title}/>
      <Container centerContent={true} m='20px'>
      <HStack spacing={4} mb='20px'>
      <Select placeholder="" style={{color:(state[0] === 'yellow'?'black':'white'), backgroundColor:state[0]}} onChange={(e)=>{ NotHidden();
                                                                                                                                setstate(prevState => [e.target.value as keyof typeof dice, prevState[1]]);
                                                                                                                                BuildField(dice[e.target.value as keyof typeof dice], dice[state[1] as keyof typeof dice], setField);
                                                                                                                                console.log(e.target.value, state[1]);
                                                                                                                                }}>
        {Object.keys(dice).map((key)=><option key={key} style={{color:'white', backgroundColor:'black'}} value={key}>{key.toUpperCase()}</option>)}
      </Select>
      <Select placeholder="" style={{color:(state[1] === 'yellow'?'black':'white'), backgroundColor:state[1]}} onChange={(e)=>{ NotHidden();
                                                                                                                                setstate(prevState => [prevState[0],e.target.value as keyof typeof dice]);
                                                                                                                                BuildField(dice[state[0] as keyof typeof dice], dice[e.target.value as keyof typeof dice], setField);                                                                                                                                
                                                                                                                                console.log(state[0], e.target.value);
                                                                                                                                }}>
        {Object.keys(dice).map((key)=><option key={key} style={{color:'white', backgroundColor:'black'}} value={key}>{key.toUpperCase()}</option>)}
      </Select>
      </HStack>
      <Text style={{color:'white', opacity:'60%', textAlign:'center'}} mt='10px' mb='25px'>
      {`The probability of one ${state[0]} die winning against one ${state[1]} die is ${probability(dice[state[0] as keyof typeof dice], dice[state[1] as keyof typeof dice])}`}
      </Text >

      {field.map((line) => (
          <HStack spacing={4} mb='15px'>
            {line.map((element)=> (
              <Text visibility={hidden?'hidden':'visible'} style={{color: state[0]===state[1]?'white':element===1?state[0]:(element===-1?state[1]:'white'), opacity:'90%', textAlign:'center'}}>
                {element===1?'X':(element===-1?'O':'=')}
              </Text>
            ))}
          </HStack>
      ))}
      

      </Container>
      
    </>
  )
}

export default NonTransitiveDice

const BuildField = (dieA:Array<number>, dieB:Array<number>, setField:React.Dispatch<React.SetStateAction<number[][]>>) => {
    
  let x = buildField();

  for(let a in dieA) {
    for(let b in dieB){
      console.log(dieA[a], dieB[b], dieA[a] > dieB[b])
      if(dieA[a] > dieB[b])
        x[a][b] = 1;
      if(dieA[a] < dieB[b])
        x[a][b] = -1;
    }
  }
  setField(x);
}


const probability = (dieA:Array<number>, dieB:Array<number>) => {
    

    let aWins = 0;
    let bWins = 0;

    for(const faceA of dieA) {
      for(const faceB of dieB){
        if(faceA > faceB)
          aWins++;
        if(faceA < faceB)
          bWins++;
        //console.log(faceA,faceB);
      }
    }
    return (
      `${aWins+bWins === 0? 0: Math.round(1000*aWins/(aWins+bWins))/10}%`
    )
}
