import { extendTheme } from "@chakra-ui/react"

export const theme = extendTheme({

  styles: {
    global: {
      // styles for the `body`
      body: {
        bg:'black',
        opacity: 0.9,
        color: "white",
      },
      
    },
  },


  components: {
    Container: {
      // 1. We can update the base styles
      baseStyle: {
        fontWeight: "bold", // Normally, it is "semibold"
      },
    },
    Input: {

    },
  },
})