import {Formik, 
    Form}  from 'formik';
import {Button, 
    Container, 
    } from "@chakra-ui/react";
import * as Yup from 'yup';
import '../style/field.scss';
import {Page} from  "../dataStructure/page";
import {CustomTextInput, CustomTitle} from '../components/custom';
import firebase from '../firebase';
import {useEffect, useState} from 'react';



interface signIn {
changePage:(page: Page) => void,
}


const  SignIn: React.FC<signIn> = ({changePage}) => {

  
  const [emailsFireStore, setEmailsFireStore ] = useState<string[]>([])

  const ref = firebase.firestore().collection('users').doc('users_document');
  console.log(ref);

  const effect = () => {
    ref.onSnapshot((querySnapshot) => {
      const emails:any[] = [];
      emails.push(querySnapshot.data()? querySnapshot.data(): '');
      setEmailsFireStore(emails[0]['email']);

    })
  }

  useEffect(() => {
    
    effect();
  }, []);

  const [state, setstate] = useState<string>('');  
  const [isEmailValid, setIsEmailValid] = useState<boolean>(false);

  interface LooseObject {
    [key: string]: any
}

  const newUser = {
    fName: '',
    lName: '',
    pssw: '',
    lists: {} as LooseObject,
    list: '',
  }


  const setState = (email:string) => {
    const refEmail = firebase.firestore().collection('users').doc('users_document').collection(email as string).doc('data');
    refEmail.get().then((doc) => {
      if (doc.exists) {
        
        setIsEmailValid(true);
        setstate((doc.data() as typeof newUser).pssw as string);
      } 
      else
        setIsEmailValid(false);
    });
  }

  
  return (
    <>
    <Formik 
    initialValues={{
        email:'',
        password:'',
    }}
    validationSchema={Yup.object({
        email: Yup.string()
            .email('Invalid email address.')
            .required('Please your email address.')
            .oneOf(emailsFireStore,'Sorry, this email is not linked to any account.'),
        password: Yup.string()
        .when('email', (email:boolean, field: any) =>
          email && isEmailValid ? field.required('Please enter your password.').oneOf([state], 'You must use the same password you inserted before.') : field
            ),

    })}
    onSubmit={(values, {setSubmitting, resetForm}) => {
        setTimeout(()=> {
            
            localStorage.setItem('email', values.email);
            changePage(Page.DashBoard);

            console.log(values);
            console.log(typeof values);
            resetForm();
            setSubmitting(false);
        }, 1500);
    }}
    >

        {props => (

            <Form>
                <Container centerContent={true}>
                <CustomTitle title='Sign In'/>                  
                <CustomTextInput label='E-mail' name='email' type='text' placeholder='example@email.com' func={setState} handleBlur={props.handleBlur}/>                 
                <CustomTextInput label='Password' name='password' type='password' placeholder='********' handleBlur={props.handleBlur}/>
                <Button type='submit' colorScheme='blue' className='button'> {props.isSubmitting? 'Loading...':'Sign In'}</Button>
                </Container>
            </Form>
        )}


    </Formik>            
    </>
)
}

export default SignIn
