import { Button, HStack, Container, Box, Checkbox, Fade, Divider } from "@chakra-ui/react";
import {Page} from  "../dataStructure/page";
import {CustomTitle} from '../components/custom';
import NewTask from "../components/newTask";  
import {DeleteIcon} from "@chakra-ui/icons";
import firebase from '../firebase';
import {useEffect, useState} from 'react';
import newUser from '../dataStructure/user';
import {saveStateAndFirestore} from '../helpers/save';

interface list {
    title:string,
    onChange: any,
}
const List:React.FC<list> = ({title, onChange}) => {   
    const email = localStorage.getItem('email');
    const ref = firebase.firestore().collection('users').doc('users_document').collection(email as string).doc('data');
        
    const [dataFireStore, setDataFireStore ] = useState<typeof newUser>(newUser);
    const [dataFireStoreBackup, SetDataFireStoreBackup ] = useState<typeof newUser>(newUser);

    const effect = () => {
      ref.onSnapshot((querySnapshot) => {
        setDataFireStore(querySnapshot.data()? querySnapshot.data() as typeof newUser: newUser);
        
        });
        ref.get().then((doc) => {
          if (doc.exists) {
            SetDataFireStoreBackup(doc.data() as typeof newUser);
          } 
        });
    }



    
    const [listName, setListName] = useState('');
    const [newItemField, setNewItemField] = useState(<></>);     
    const [newItem, setNewItem] = useState('');  
    const [addText, setAddText] = useState('add');
    const [option, setOption] = useState('0');
    const [buttonVisibility, setButtonVisibility] = useState(true)

    const MAX_DIST = 35000;
    const mouse_Movement = (e:MouseEvent)=> {
      const save = document.getElementById('newItem');
      if(save && window.innerWidth > 500){
        const [saveX,saveY]=[save.getBoundingClientRect().x,save.getBoundingClientRect().y];
        const d =(e.clientX-saveX-100)*(e.clientX-saveX-100)  + (e.clientY-saveY-10)*(e.clientY-saveY-10);
        setButtonVisibility( d < MAX_DIST);
      }
      else  
        setButtonVisibility(true);
      
    }

    useEffect(() => {
      effect();     
      window.addEventListener('mousemove', mouse_Movement);     
      return () => {
        window.removeEventListener('mousemove', mouse_Movement);
      };                         
    }, []);




    return (
        <>    
        <Box ml ='70%' mt='10px' >
            
        <HStack spacing={3}> 
            <Button  colorScheme="blue" size="xs" onClick={() => {
                        setNewItemField(
                                        
                                        <NewTask onChange={setListName} type={'to-do list'}/>
                                                                        
                                    );
                                    setOption('2');
                                    setAddText('add');
                                    setTimeout(() =>
                                    (document.getElementById("to-do list") as HTMLElement).focus(), 100); 
                    }}>
                        Rename
            </Button>

            <Button  colorScheme="blue" size="xs" onClick={() => {
                                                        const data = dataFireStoreBackup;
                                                        data.list = '';                                                     
                                                       saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                                                        onChange(Page.DashBoard);}}>
                Cancel
            </Button> 
            </HStack>
        </Box>


            <CustomTitle title={dataFireStore.list}/>
            

            
            <Box id='box' mb={'10px'}>
            <HStack spacing={1}> 
                {newItemField}
                <Button  visibility={newItem === '' && listName === ''? 'collapse': 'visible'} colorScheme="blue" onClick={ () => {
                                        setAddText('<<');

                                        if(option === '1'){
                                            const data = dataFireStore;
                                            data.lists[data.list] =[...data.lists[data.list].concat([`${newItem}!***!false!***!`])];
                                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                                        }
                                        if(option === '2'){
                                            const data = dataFireStore;
                                            
                                            if(!(listName in data.lists)){
                                            data.list = listName;
                                            data.lists[listName] = data.lists[dataFireStoreBackup.list];
                                            delete data.lists[dataFireStoreBackup.list];

                                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                                            }
                                        }
                                        

                                        setNewItem('');                                        
                                        setListName('');
                                        setNewItemField(<></>);
                                        setOption('0');
                                    }}>
                                    {addText}
                                    </Button>
            </HStack>
            </Box>            

            <Container centerContent={true}>    
            
            <Fade in={buttonVisibility} id='newItem'>
              
            <HStack spacing={8}>   
                <Button  colorScheme="blue" onClick={() => {
                    setNewItemField(
                                    
                                    <NewTask onChange={setNewItem} type={'new item'}/>
                                                                      
                                );
                                setOption('1');
                                setAddText('add');
                                setTimeout(() =>
                                (document.getElementById("new item") as HTMLElement).focus(), 100); 
                }}>
                    New Item
                </Button>
             
            <Button  id='save' colorScheme="blue"  onClick={() => {
                                                            
                                                            onChange(Page.DashBoard);
                                                            
                                                            }}>
                Save
            </Button>  
                           
            </HStack>
            </Fade>
                         
            </Container>

            <Box id='box' m={'20px'} style={{width: '80%', backgroundColor: '#dcecf7', borderRadius:'20px' }}>
                
                {dataFireStore.lists[dataFireStore.list] !== undefined? (dataFireStore.lists[dataFireStore.list] as string[]).map((item, i) => 
                    <div  draggable="true"  key={i} style={{color: "#3182ce", fontSize:"20px", margin: '10px'}} >
                        
                        <HStack spacing={5} >
                        <DeleteIcon color="gray.400" onClick={() => {
                            const aux1 = [...(dataFireStore.lists[dataFireStore.list]).slice(0,i)];
                            const aux2 = [...(dataFireStore.lists[dataFireStore.list]).slice(i+1,(dataFireStore.lists[dataFireStore.list]).length)];

                            const data = dataFireStore;
                            data.lists[data.list] = aux1.concat(aux2);
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                              
                        }}/>
                        <Checkbox  isChecked={item.split('!***!')[1]=== 'true'} colorScheme="blue" defaultIsChecked={item[1] === 'true'} style={{borderColor : '#3182ce'}}
                        onChange={() => {
                            const auxCheck = [...dataFireStore.lists[dataFireStore.list]];
                            const aux = auxCheck[i].split('!***!')[0]; 
                            const auxLink = auxCheck[i].split('!***!')[2]?auxCheck[i].split('!***!')[2]:'';                        
                            auxCheck[i] = auxCheck[i].split('!***!')[1] === 'true'? `${aux}!***!false!***!${auxLink}`:`${aux}!***!true!***!${auxLink}`;

                            const data = dataFireStore;
                            data.lists[data.list] = auxCheck;
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                        }}> 
                        {`${item.split('!***!')[0]}`}
                        
                        </Checkbox>
                        <a href={item.split('!***!')[2]? item.split('!***!')[2]:''}>{item.split('!***!')[2]?'(link)':null}</a>
                            
                        </HStack>
                        
                        
                    </div>
                ):null}
            </Box>


            <Container centerContent={true}>
            <HStack spacing={8}>
              
             
            </HStack>
            </Container>
        </>
    );
}

export default List;