import firebase from "firebase/app";
import newUser from '../dataStructure/user';

interface SaveStateAndFirestore{
  ref:firebase.firestore.DocumentData;
  setState: React.Dispatch<React.SetStateAction<typeof newUser>>;
  newState: typeof newUser;

}

export const saveStateAndFirestore=({ref, setState, newState}:SaveStateAndFirestore) => {
  setState(newState);
  ref.set(
    newState
  ).then(() => {
    console.log('DONE DATA');
  });
}
